* {
  -ms-user-select: none;
  user-select: none;
}

:focus {
  outline: none;
}

.container {
  text-align: center;
  margin: 0 auto;
}

.cat-info {
  justify-content: center;
  margin: 25px;
  display: flex;
}

.breed-select {
  width: 1025px;
  height: auto;
  cursor: pointer;
  background: inherit;
  text-align: center;
  border: 3px double green;
  font-size: large;
}

.thumb {
  max-width: 600px;
  border: 3px double green;
  margin-left: 25px;
  padding: 5px;
}

.thumb-pic {
  position: relative;
  overflow: hidden;
}

img {
  border: 3px double green;
  display: block;
}

.thumb-pic:before {
  z-index: 2;
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
  display: block;
  position: absolute;
  top: 0;
  left: -75%;
  transform: skewX(-25deg);
}

.thumb-pic:hover:before {
  animation: shine .75s;
}

@keyframes shine {
  100% {
    left: 125%;
  }
}

.option:first-child {
  pointer-events: none;
}

.option {
  transition: background-color .3s ease-in-out;
}

.option:hover {
  color: #fff;
}

.option:nth-child(7n+1):hover {
  background-color: #000;
}

.option:nth-child(7n+2):hover {
  background-color: #ea4c89;
}

.option:nth-child(7n+3):hover {
  background-color: #32c766;
}

.option:nth-child(7n+4):hover {
  background-color: #04266b;
}

.option:nth-child(7n+5):hover {
  background-color: #f48024;
}

.option:nth-child(7n+6):hover {
  background-color: #006400;
}

.option:nth-child(7n+7):hover {
  background-color: red;
}

